.getPhotosPage__Photos {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.photoOption {
    width: 275px;
    margin: 0 0 30px;
    border: none;
}

.photoOption__Image {
    position: relative;
    border-radius: 10px;
    overflow: hidden;
    transform: translateY(0);
    transition: all 0.3s;
}

.photoOption__Image:hover .photoOverlay {
    opacity: 1;
}
.photoOption img {
	width: 275px;
	height: 184px;
    box-shadow: 0px 5px 10px transparent;
}

.photoOption__Image:hover {
	cursor: pointer;
	box-shadow: 0px 5px 10px #222;
	transform: translateY(-5px);
}

.photoOption__Image:focus {
    border: 4px solid #ef6461;
    border-radius: 5px;
    outline: none;
}

.photoOverlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(rgba(91,216,247,0.5), rgba(91,216,247,0.5));
    opacity: 0;
}

.photoOverlay p {
    color: #f7f0f0;
    letter-spacing: 2px;
    font-size: 1.2rem;
}

.getPhotosPage a {
    display: inline-block;
    width: 100%;
    margin: 5px 0 0;
    text-align: center;
    color: #484349;
    font-size: 0.8rem;
}

.getPhotosPage a:hover{
    color: #ef6461;   
}

.getPhotosPage a:focus{
    color: #ef6461;   
    outline: none;
}

.getPhotosPage h2 {
    font-family: 'Bungee', cursive;
    text-align: center;
    letter-spacing: 3px;
    font-size: 3rem;
    color: #ef6461;
    margin: 40px 0 0;
}

.getPhotosPage h3 {
    text-align: center;
    font-size: 1.2rem;
}


.getPhotosPage form {
    display: flex;
    justify-content: center;
    margin: 0 0 30px;
}

.getPhotosPage input[type='text'] {
    border: 2px solid #ef6461;
    border-radius: 5px;
    padding: 2px 5px;
    background: #f7f0f0;
    margin-right: 10px;
    width: 25%;
}

.getPhotosPage input[type='submit'] {
    border: none;
    border-radius: 5px;
    background: #ef6461;
    padding: 2px 15px;
    color: #f7f0f0;
    transition: all 0.5s;
}

.getPhotosPage input[type='text']:focus {
    border: 2px solid #5bd8f7;
}

.getPhotosPage input[type='submit']:focus,
.getPhotosPage input[type='submit']:hover {
   background: #5bd8f7;
}

@media (max-width: 1350px) {
    .getPhotosPage__Photos {
        justify-content: space-around;
        width: 90%;
        margin: 0 auto;
    }
}

@media (max-width: 1140px) {
    .getPhotosPage__Photos {
        width: 80%;
    }
    .getPhotosPage input[type='text'] {
        width: 30%;
    }
}

@media (max-width: 900px) {
    .getPhotosPage__Photos {
        width: 100%;
        flex-direction: column;
        align-items: center;
    }
    .getPhotosPage input[type='text'] {
        width: 40%;
    }
}

@media (max-width: 500px) {
    .getPhotosPage h2 {
        font-size: 2rem;
    }
    .getPhotosPage h3 {
        font-size: 0.8rem;
    }
    .getPhotosPage input[type='text'] {
        width: 60%;
    }
}